import React, { useState, useContext } from 'react';
import Signup from "./Signup";
import {Box, Button, IconButton, InputAdornment, TextField} from "@mui/material";
import {Visibility, VisibilityOff} from "@mui/icons-material";
import { UserContext } from './Usercontext'
import axios from "axios";
import { useNavigate } from 'react-router-dom';

export default function Login() {
    
  const { userData, setUserData, isLoggedIn, setIsLoggedIn } = useContext(UserContext); // UserContext  
  const [isSignUp, setIsSignUp] = useState(false); //   
  const [showPassword, setShowPassword] = useState(false); //  
  const navigate = useNavigate();
  
  /*   */
  const handleLogin = (event) => {
    event.preventDefault();
    axios.post('/api/member/login', {
        memberEmail: event.target.memberEmail.value,
        memberPassword: event.target.memberPassword.value,
    })
        .then((response) => {
            if(response.data.memberEmail === event.target.memberEmail.value && response.data.memberPassword === event.target.memberPassword.value){
                setUserData({memberEmail: response.data.memberEmail,
                    memberName: response.data.memberName
                });
                sessionStorage.setItem("userData", JSON.stringify(response.data));
                setIsLoggedIn(true);
                navigate(`/Project`);
            }
            else {
                alert('   .');
                navigate(``);
            }
        })
        .catch(function (error) {
            console.log(error);
            alert('  .');
            navigate(``);
        });
  };

    /*   */
    const handleLogout = () => {
        axios.post('/api/member/logout')
            .then((response) => {
                setIsLoggedIn(false);
                //   
                sessionStorage.removeItem('userData');
                sessionStorage.removeItem('currentProjectId');
                sessionStorage.removeItem('currentIssueId');
                setUserData({});
                navigate(``);
            })
            .catch(function (error) {
                console.log(error);
                alert('  .');
                navigate(``);
            });
    };


  //    
    if (isLoggedIn) {
        return (
            <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center', mt: 2, padding: '40px', textAlign: 'center'}}>
                <h1>!<br/>{userData.memberName}</h1>
                <Button onClick={handleLogout} variant="contained" sx={{
                    backgroundColor: '#03C75A', //  
                    '&:hover': {
                        backgroundColor: '#03C75A', //   
                    },
                }}>
                    
                </Button>
            </Box>
        );
    }

  /*   */
  const handleSignUp = (event) => {
    event.preventDefault();
    setIsSignUp(true);
  }

    const handleClickShowPassword = () => {
        setShowPassword(!showPassword);
    };

    const handleMouseDownPassword = (event) => {
        event.preventDefault();
    };

  if(isSignUp){
    return (<Signup/>);
  }


  /* */
  return (
      <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center', mt: 2, padding: '40px'}}>
          <form id='loginform' onSubmit={handleLogin} method="post">
              <TextField
                  label=""
                  name="memberEmail"
                  fullWidth
                  margin="none"
                  color="success"
                  sx={{
                      '& .MuiOutlinedInput-root': {
                          '&.Mui-focused fieldset': {
                              borderColor: '#03C75A', //    
                          },
                          '& .MuiInputLabel-root': {
                              color: '#03C75A', //   
                          },
                      },
                  }}
              />
              <TextField
                  label=""
                  name="memberPassword"
                  type={showPassword ? 'text' : 'password'}
                  fullWidth
                  margin = 'normal'
                  color="success"
                  sx={{
                      '& .MuiOutlinedInput-root': {
                          '&.Mui-focused fieldset': {
                              borderColor: '#03C75A', //    
                          },
                          '& .MuiInputLabel-root': {
                              color: '#03C75A', //   
                          },
                      },
                  }}
                  InputProps={{
                      endAdornment: (
                          <InputAdornment position="end">
                              <IconButton
                                  aria-label="toggle password visibility"
                                  onClick={handleClickShowPassword}
                                  onMouseDown={handleMouseDownPassword}
                                  edge="end"
                              >
                                  {showPassword ? <VisibilityOff /> : <Visibility />}
                              </IconButton>
                          </InputAdornment>
                      ),
                  }}
              />
              <Button type="submit" variant="contained" fullWidth margin="normal" sx={{
                  backgroundColor: '#03C75A', //  
                  '&:hover': {
                      backgroundColor: '#03C75A', //   
                  },
              }}>
                  
              </Button>
          </form>
          <Button onClick={handleSignUp} color="primary">
            
          </Button>
        </Box>
  );
}