import React, { useContext, useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { Paper, Typography, Box, Button, TextField, Autocomplete } from '@mui/material';
import axios from 'axios';
import Chip from "@mui/material/Chip";
import { UserContext } from "./Usercontext";

//  
const options = [
    "", " ", " ", " ", "  ", " ",
    " ", "  ", " ", " ", " ", " ",
    " ", " ", " ", " ", " ", "  ",
    "  ", "  ", ""
];

const IssueUpdate = () => {
    const location = useLocation();
    const navigate = useNavigate();
    const { currentProject, currentIssue, userRole } = location.state || { currentProject: {}, currentIssue: {}, userRole: '' }; // location.state currentProject, currentIssue, userRole 
    const { userData } = useContext(UserContext);

    //     
    const [issueTitle, setIssueTitle] = useState(currentIssue.issueTitle || '');
    const [issueDescription, setIssueDescription] = useState(currentIssue.issueDescription || '');
    const [keyword, setKeyword] = useState(currentIssue.keyWords || []);
    const [priority, setPriority] = useState(currentIssue.priority || '');
    const [status, setStatus] = useState(currentIssue.status || '');
    const [assignee, setAssignee] = useState(currentIssue.assignee || '');
    const [comment, setComment] = useState('');

    const [recommends, setRecommends] = useState(currentProject.devUser || []); //      
    const statusOptions = ['new', 'open', 'in-progress', 'fixed', 'resolved', 'closed', 'reopened'];

    //   option   
    const isOptionDisabled = (option) => {
        if (option === 'new') return true;
        if (userRole === 'devUser' && option !== 'fixed') return true;
        if (userRole === 'testUser' && currentIssue.reporter === userData.memberName) {
            if (option !== 'resolved' && option !== 'reopened') return true;
        }
        return (userRole === 'plUser' && option !== 'closed');
    };

    //   
    const tagDisabled = () => {
        return !(userRole === 'testUser' && currentIssue.reporter === userData.memberName);
    }
    const comboDisabled = () => {
        return !(userRole === 'plUser');
    }

    //   
    if (!currentIssue.issueTitle) {
        alert(" !");
        navigate(`/Project/${currentProject.projectTitle}`);
    }

    //  
    const recommendDeveloper = () => {
        axios.post(`/api/projects/${currentProject.id}/issues/${currentIssue.id}/recommend`, { keyword })
            .then(response => {
                setRecommends(response.data.devUser);
            })
            .catch(error => {
                console.error('Error recommending developer:', error);
            });
    };

    //  
    const handleUpdate = (e) => {
        e.preventDefault();
        axios.post(`/api/projects/${currentProject.id}/issues/${currentIssue.id}/update`, {
            issueTitle: issueTitle,
            issueDescription: issueDescription,
            keyWords: keyword,
            priority: priority,
            status: status
        })
            .then(response => {
                if (userRole === 'plUser') {
                    axios.post(`/api/projects/${currentProject.id}/issues/${currentIssue.id}/update-dev`, { assignee })
                        .catch(error => {
                            console.error('assign Error:', error);
                        })
                }
                if (comment) {
                    axios.post(`/api/projects/${currentProject.id}/issues/${currentIssue.id}/comments/create`, {
                        content: comment
                    })
                        .then(response => {
                            navigate(`/Project/${currentProject.projectTitle}`, {
                                state: { currentProject, currentIssue: response.data }
                            });
                        })
                        .catch(error => {
                            console.error('Error creating comment:', error);
                        });
                } else {
                    navigate(`/Project/${currentProject.projectTitle}/`, {
                        state: { currentProject, currentIssue: response.data }
                    });
                }
            })
            .catch(error => {
                console.error('Error updating issue:', error);
            });
    };

    //  
    const handleStatusChange = (event, newValue) => {
        setStatus(newValue);
    };

    //  , , , ,  
    const handleInputChange = (e) => {
        const { name, value } = e.target;
        switch (name) {
            case 'issueTitle':
                setIssueTitle(value);
                break;
            case 'issueDescription':
                setIssueDescription(value);
                break;
            case 'keyword':
                setKeyword(value.split(','));
                break;
            case 'priority':
                setPriority(value);
                break;
            default:
                break;
        }
    };

    return (
        <Paper sx={{ width: '90%', padding: 3, margin: 'auto', maxWidth: 800 }}>
            <Typography variant="h4" gutterBottom>
                 
            </Typography>
            <Box component="form" sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }} onSubmit={handleUpdate}>
                <TextField
                    label=" "
                    name="issueTitle"
                    color='success'
                    value={issueTitle}
                    onChange={handleInputChange}
                    margin="normal"
                    fullWidth
                    disabled={tagDisabled()}
                    sx={{
                        '& .MuiOutlinedInput-root': {
                            '&.Mui-focused fieldset': {
                                borderColor: '#03C75A', //    
                            },
                            '& .MuiInputLabel-root': {
                                color: '#03C75A', //   
                            },
                        },
                    }}
                />
                <TextField
                    label=" "
                    name="issueDescription"
                    color='success'
                    value={issueDescription}
                    onChange={handleInputChange}
                    margin="normal"
                    fullWidth
                    multiline
                    rows={6}
                    disabled={tagDisabled()}
                    sx={{
                        '& .MuiOutlinedInput-root': {
                            '&.Mui-focused fieldset': {
                                borderColor: '#03C75A', //    
                            },
                            '& .MuiInputLabel-root': {
                                color: '#03C75A', //   
                            },
                        },
                    }}
                />
                <Autocomplete
                    multiple
                    id="keyword"
                    options={options}
                    required
                    value={keyword}
                    fullWidth
                    margin="normal"
                    onChange={(event, newValue) => {
                        setKeyword(newValue);
                    }}
                    freeSolo
                    disabled={tagDisabled()}
                    isOptionEqualToValue={(option, value) => option === value || value === "" || option === ""}
                    renderTags={(value, getTagProps) =>
                        value.map((option, index) => (
                            <Chip key={index} variant="outlined" label={option} {...getTagProps({ index })} />
                        ))
                    }
                    renderInput={(params) => (
                        <TextField
                            {...params}
                            color="success"
                            variant="outlined"
                            sx={{
                                '& .MuiOutlinedInput-root': {
                                    '&.Mui-focused fieldset': {
                                        borderColor: '#03C75A', //    
                                    },
                                },
                            }}
                            label=""
                            margin="normal"
                        />
                    )}
                />
                <Autocomplete
                    id="priority"
                    options={["blocker", "critical", "major", "minor", "trivial"]}
                    required
                    disabled={tagDisabled()}
                    value={priority}
                    onChange={(event, newValue) => {
                        setPriority(newValue);
                    }}
                    sx={{ width: '25%', alignSelf: 'start' }}
                    margin="normal"
                    disablePortal
                    renderInput={(params) => (
                        <TextField
                            {...params}
                            color="success"
                            variant="outlined"
                            sx={{
                                '& .MuiOutlinedInput-root': {
                                    '&.Mui-focused fieldset': {
                                        borderColor: '#03C75A', //    
                                    },
                                    '&:hover fieldset': {
                                        borderColor: '#03C75A', // hover   
                                    },
                                },
                            }}
                            label=""
                            margin="normal"
                        />
                    )}
                />
                <Autocomplete
                    options={statusOptions}
                    value={status}
                    sx={{ width: '25%', alignSelf: 'start' }}
                    onChange={handleStatusChange}
                    getOptionDisabled={isOptionDisabled}
                    renderInput={(params) => (
                        <TextField
                            {...params}
                            label=""
                            margin="normal"
                            fullWidth
                            color='success'
                            sx={{
                                '& .MuiOutlinedInput-root': {
                                    '&.Mui-focused fieldset': {
                                        borderColor: '#03C75A', //    
                                    },
                                    '& .MuiInputLabel-root': {
                                        color: '#03C75A', //   
                                    },
                                },
                            }}
                        />
                    )}
                />

                <Box sx={{ display: 'flex', alignItems: 'center', width: '100%' }}>
                    <Autocomplete
                        id="assignee"
                        options={recommends}
                        required
                        disabled={comboDisabled}
                        value={assignee}
                        onChange={(event, newValue) => {
                            setAssignee(newValue);
                        }}
                        sx={{ width: '25%' }}
                        margin="normal"
                        disablePortal
                        renderInput={(params) => (
                            <TextField
                                {...params}
                                color="success"
                                variant="outlined"
                                sx={{
                                    '& .MuiOutlinedInput-root': {
                                        '&.Mui-focused fieldset': {
                                            borderColor: '#03C75A', //    
                                        },
                                        '&:hover fieldset': {
                                            borderColor: '#03C75A', // hover   
                                        },
                                    },
                                }}
                                label=""
                                margin="normal"
                            />
                        )}
                    />
                    {userRole === 'plUser' && (
                        <>
                            <Button
                                onClick={recommendDeveloper}
                                variant="contained"
                                sx={{
                                    ml: 2,
                                    mt: 1,
                                    backgroundColor: '#03C75A', //  
                                    '&:hover': {
                                        backgroundColor: '#03C75A', //   
                                    },
                                }}
                            >
                                
                            </Button>
                        </>
                    )}
                </Box>
                <TextField
                    label=""
                    color="success"
                    name="comment"
                    value={comment}
                    onChange={(e) => setComment(e.target.value)}
                    margin="normal"
                    fullWidth
                    multiline
                    rows={2}
                    sx={{
                        '& .MuiOutlinedInput-root': {
                            '&.Mui-focused fieldset': {
                                borderColor: '#03C75A', //    
                            },
                            '& .MuiInputLabel-root': {
                                color: '#03C75A', //   
                            },
                        },
                    }}
                />
                <Button type="submit" variant="contained" sx={{
                    mt: 3, marginTop: 2, backgroundColor: '#03C75A', //  
                    '&:hover': {
                        backgroundColor: '#03C75A', //   
                    },
                }}>
                    
                </Button>
            </Box>
        </Paper>
    );
};

export default IssueUpdate;
