// UserContext.js
import React, {createContext, useEffect, useState} from 'react';

export const UserContext = createContext(null);

export const UserProvider = ({ children }) => {

    /* */
    const storedUserData = window.sessionStorage.getItem('userData'); //    
    const [userData, setUserData] = useState(JSON.parse(storedUserData) || {})
    const [isLoggedIn, setIsLoggedIn] = useState(false); //   

    useEffect(() => {
        if (!userData || Object.keys(userData).length === 0) {
            setIsLoggedIn(false);
        }
        else{
            setIsLoggedIn(true);
        }
    }, [userData]);

    /*    */
    const accessProjectId = window.sessionStorage.getItem('currentProjectId'); //   id
    const [currentProjectId, setCurrentProjectId] = useState(accessProjectId || null); //    id

    /*   */
    const accessIssueId = window.sessionStorage.getItem('currentIssueId');
    const [currentIssueId,setCurrentIssueId] = useState(accessIssueId || null); //    id


    /*   */
    return (
        <UserContext.Provider value={{ userData, setUserData, isLoggedIn, setIsLoggedIn, currentProjectId, setCurrentProjectId, currentIssueId, setCurrentIssueId}}>
            {children}
        </UserContext.Provider>
    );
};
