import React, { useState } from 'react';
import axios from "axios";
import { useLocation, useNavigate } from 'react-router-dom';
import Paper from "@mui/material/Paper";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";
import Autocomplete from '@mui/material/Autocomplete';
import Chip from '@mui/material/Chip';

//  
const options = [
    "", " ", " ", " ", "  ", " ",
    " ", "  ", " ", " ", " ", " ",
    " ", " ", " ", " ", " ", "  ",
    "  ", "  ", ""
];

const IssueCreate = () => {
    const location = useLocation();
    const [issueTitle, setIssueTitle] = useState('');
    const [issueDescription, setIssueDescription] = useState('');
    const { currentProject } = location.state || {};
    const navigate = useNavigate();
    const [keyword, setKeyword] = useState([]);
    const [priority, setPriority] = useState('major');

    const handleCreate = () => {
        axios.post(`/api/projects/${currentProject.id}/issues/create`, {
            issueTitle: issueTitle,
            issueDescription: issueDescription,
            priority: priority,
            status: "",
            keyWords: keyword
        })
            .then(() => {
                alert('  .');
                navigate(`/Project/${currentProject.projectTitle}`);
            })
            .catch((error) => {
                console.log(error);
                alert('  .');
            });
    };

    return (
        <Paper sx={{ width: '90%', margin: 'auto', padding: 4 }}>
            <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center', mt: 4 }}>
                <Typography variant="h5" sx={{ mb: 2, fontWeight: 'bold' }}> </Typography>
                <TextField
                    label=""
                    value={issueTitle}
                    onChange={(e) => setIssueTitle(e.target.value)}
                    variant="outlined"
                    margin="normal"
                    fullWidth
                    required
                    color="success"
                    sx={{
                        '& .MuiOutlinedInput-root': {
                            '&.Mui-focused fieldset': {
                                borderColor: '#03C75A', //    
                            },
                        },
                    }}
                />
                <TextField
                    label=""
                    value={issueDescription}
                    onChange={(e) => setIssueDescription(e.target.value)}
                    variant="outlined"
                    margin="normal"
                    fullWidth
                    multiline
                    required
                    rows={6}
                    color="success"
                    sx={{
                        '& .MuiOutlinedInput-root': {
                            '&.Mui-focused fieldset': {
                                borderColor: '#03C75A', //    
                            },
                        },
                    }}
                />
                {/*keyword Autocomplete */}
                <Autocomplete
                    multiple
                    id="keyword"
                    options={options}
                    required
                    value={keyword}
                    fullWidth
                    margin="normal"
                    onChange={(event, newValue) => {
                        setKeyword(newValue);
                    }}
                    freeSolo
                    renderTags={(value, getTagProps) =>
                        value.map((option, index) => (
                            <Chip key={index} variant="outlined" label={option} {...getTagProps({ index })} />
                        ))
                    }
                    renderInput={(params) => (
                        <TextField
                            {...params}
                            color="success"
                            variant="outlined"
                            sx={{
                                '& .MuiOutlinedInput-root': {
                                    '&.Mui-focused fieldset': {
                                        borderColor: '#03C75A', //    
                                    },
                                },
                            }}
                            label=""
                            required
                            margin="normal"
                        />
                    )}
                />
                <Autocomplete
                    id="priority"
                    options={["blocker", "critical", "major", "minor", "trivial"]}
                    required
                    value={priority}
                    onChange={(event, newValue) => {
                        setPriority(newValue);
                    }}
                    sx={{ width: '25%' }}
                    margin="normal"
                    disablePortal
                    renderInput={(params) => (
                        <TextField
                            {...params}
                            color="success"
                            variant="outlined"
                            sx={{
                                '& .MuiOutlinedInput-root': {
                                    '&.Mui-focused fieldset': {
                                        borderColor: '#03C75A', //    
                                    },
                                    '&:hover fieldset': {
                                        borderColor: '#03C75A', // hover   
                                    },
                                },
                                width: '150px'
                            }}
                            label=""
                            required
                            margin="normal"
                        />
                    )}
                />
                <Button variant="contained" onClick={handleCreate} sx={{
                    mt: 3,
                    backgroundColor: '#03C75A', //  
                    '&:hover': {
                        backgroundColor: '#03C75A', //   
                    },
                }}>
                    
                </Button>
            </Box>
        </Paper>
    );
};

export default IssueCreate;
