import React, { useState } from 'react';
import { TextField, Button, Box, IconButton, InputAdornment} from '@mui/material';
import { Visibility, VisibilityOff} from "@mui/icons-material";
import axios from "axios";

//  
export default function Signup() {
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [name, setName] = useState('');
    const [showPassword, setShowPassword] = useState(false);

    // db  
    const addUser = (user) => {
        axios.post('/api/member/save',{
            memberEmail: user.email,
            memberPassword: user.password,
            memberName: user.name
            }
        )
            .then(function (response) {
                console.log(response);
                alert(" .");
            })
            .catch(function (error) {
                console.log(error);
                alert(" .");
            });
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        addUser({ name, email, password }); // 
        window.location.reload(); //   
    };

    //  *  
    const handleClickShowPassword = () => {
        setShowPassword(!showPassword);
    };

    const handleMouseDownPassword = (event) => {
        event.preventDefault();
    };

    return (
        <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center', mt: 2, padding: '40px'}}>
            <form onSubmit={handleSubmit}>
                <TextField
                    label=""
                    value={name}
                    onChange={(e) => setName(e.target.value)}
                    required
                    fullWidth
                    color="success"
                    margin="dense"
                    sx={{
                        '& .MuiOutlinedInput-root': {
                            '&.Mui-focused fieldset': {
                                borderColor: '#03C75A', //    
                            },
                            '& .MuiInputLabel-root': {
                                color: '#03C75A', //   
                            },
                        },
                    }}
                />
                <TextField
                    label=""
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    required
                    fullWidth
                    color="success"
                    margin="dense"
                    sx={{
                        '& .MuiOutlinedInput-root': {
                            '&.Mui-focused fieldset': {
                                borderColor: '#03C75A', //    
                            },
                            '& .MuiInputLabel-root': {
                                color: '#03C75A', //   
                            },
                        },
                    }}
                />
                <TextField
                    label=""
                    type={showPassword ? 'text' : 'password'}
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    required
                    fullWidth
                    color="success"
                    margin="dense"
                    sx={{
                        '& .MuiOutlinedInput-root': {
                            '&.Mui-focused fieldset': {
                                borderColor: '#03C75A', //    
                            },
                            '& .MuiInputLabel-root': {
                                color: '#03C75A', //   
                            },
                        },
                    }}
                    InputProps={{
                        endAdornment: (
                            <InputAdornment position="end">
                                <IconButton
                                    aria-label="toggle password visibility"
                                    onClick={handleClickShowPassword}
                                    onMouseDown={handleMouseDownPassword}
                                    edge="end"
                                >
                                    {showPassword ? <VisibilityOff /> : <Visibility />}
                                </IconButton>
                            </InputAdornment>
                        ),
                    }}
                />
                <Button type="submit" variant="contained" fullWidth margin='normal' sx={{
                    backgroundColor: '#03C75A', //  
                    '&:hover': {
                        backgroundColor: '#03C75A', //   
                    },
                }}>
                    
                </Button>
            </form>
        </Box>
    );
}