import React, { useEffect, useContext, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Paper, Typography, Box, Button, Divider, TextField } from '@mui/material';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import { styled } from '@mui/material/styles';
import axios from 'axios';
import { UserContext } from './Usercontext';
import SpeedDial from '@mui/material/SpeedDial';
import SpeedDialAction from '@mui/material/SpeedDialAction';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import MoreVertIcon from '@mui/icons-material/MoreVert';

const StyledTableRow = styled(TableRow)(({ theme }) => ({
    '&:nth-of-type(odd)': {
        backgroundColor: theme.palette.action.hover,
    },
}));

const StyledSpeedDial = styled(SpeedDial)(({theme}) => ({
    '& .MuiSpeedDial-fab': {
        backgroundColor: '#03C75A',
        padding: 'none',
        boxShadow: 'none',
        '&:hover': {
            backgroundColor: '#03C75A',
            padding: 'none'
        },
    },
}));

const actions = [
    { icon: <EditIcon />, name: '' },
    { icon: <DeleteIcon />, name: '' }
];

const IssueDetail = () => {
    const navigate = useNavigate();
    const [currentProject, setCurrentProject] = useState({});
    const [currentIssue, setCurrentIssue] = useState({});
    const [page, setPage] = useState(0);
    const rowsPerPage = 5;
    const columns = [
        { id: 'memberEntity', label: '', minWidth: 170, align: 'left' },
        { id: 'content', label: '', minWidth: 170, align: 'center' },
        { id: 'reportedDate', label: '', minWidth: 170, align: 'right' }
    ];
    const { userData, currentProjectId, currentIssueId } = useContext(UserContext); // UserContext  

    // dial 
    const [open, setOpen] = useState(false);

    const handleOpen = () => setOpen(true);
    const handleClose = () => setOpen(false);

    const [rows, setRows] = useState([]);

    // Project  Issue     
    useEffect(() => {
        if (currentProjectId || currentIssueId) {
            axios.get(`/api/projects/${currentProjectId}`)
                .then((response) => {
                    setCurrentProject(response.data);
                    axios.get(`/api/projects/${currentProjectId}/issues/${currentIssueId}`)
                        .then((response) => {
                            setCurrentIssue(response.data);
                        })
                        .catch((error) => {
                            console.error('Error fetching issue data:', error);
                            navigate('/Project');
                        });
                })
                .catch((error) => {
                    console.error('Error fetching project data:', error);
                    navigate('/Project');
                });
        }
    }, [currentProjectId, currentIssueId, navigate]);

    //     
    useEffect(() => {
        if (currentIssue && currentIssue.id) {
            axios.get(`/api/projects/${currentProject.id}/issues/${currentIssue.id}/comments`)
                .then((response) => {
                    setRows(response.data);
                })
                .catch((error) => {
                    console.error('Error fetching comments:', error);
                });
        }
    }, [currentProject, currentIssue]);

    //  
    const formattedKeywords = Array.isArray(currentIssue.keyWords) && currentIssue.keyWords.length !== 0
        ? currentIssue.keyWords.map(keyword => `# ${keyword}`).join(', ')
        : '';


        //   
    const [newComment, setNewComment] = useState('');
    const handleCommentSubmit = (e) => {
        e.preventDefault();
        axios.post(`/api/projects/${currentProject.id}/issues/${currentIssue.id}/comments/create`, {
            content: newComment
        })
            .then((response) => {
                setNewComment('');
                navigate(0);
            })
            .catch((error) => {
                console.log(error);
                alert('  ');
            });
    };

    //       
    const handleLoadMore = () => {
        setPage(prevPage => prevPage + 1);
    };

    const displayedRows = rows.slice(0, (page + 1) * rowsPerPage);

    //     
    const handleEditClick = () => {
        let userRole = 'none';
        if (currentProject.plUser.includes(userData.memberName)) {
            userRole = 'plUser';
        } else if (currentProject.devUser.includes(userData.memberName)) {
            userRole = 'devUser';
        } else if (currentProject.testUser.includes(userData.memberName) && currentIssue.reporter === userData.memberName) {
            userRole = 'testUser';
        }

        if (userRole !== 'none') {
            navigate(`/Project/${currentProject.projectTitle}/${currentIssue.issueTitle}/IssueUpdate`, {
                state: { currentProject, currentIssue, userRole }
            });
        } else {
            alert("  .");
        }
    };

    return (
        <Paper sx={{ width: '100%', padding: 3, margin: 'auto', maxWidth: 1000 }}>
            <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: 2 }}>
                <Typography variant="h4" align="center" gutterBottom>
                    {currentIssue.issueTitle}
                </Typography>
                <StyledSpeedDial
                    ariaLabel="menu"
                    icon={<MoreVertIcon />}
                    direction="down"
                    open={open}
                    onOpen={handleOpen}
                    onClose={handleClose}
                    onClick={handleOpen}
                >
                    {actions.map((action) => (
                        <SpeedDialAction
                            key={action.name}
                            icon={action.icon}
                            tooltipTitle={action.name}
                            onClick={(event) => handleEditClick(event, action.name)}
                        />
                    ))}
                </StyledSpeedDial>
            </Box>
            <Box sx={{ marginBottom: 2 }}>
                <Typography variant="body1" gutterBottom>
                    {currentIssue.issueDescription}
                </Typography>
                <Typography variant="body2" color="textSecondary">
                    Reporter: {currentIssue.reporter}
                </Typography>
                <Typography variant="body2" color="textSecondary">
                    Fixer: {currentIssue.fixer}
                </Typography>
                <Typography variant="body2" color="textSecondary">
                    Assignee: {currentIssue.assignee}
                </Typography>
                <Typography variant="body2" color="textSecondary">
                    Reported Date: {currentIssue.reportedDate}
                </Typography>
                <Typography variant="body2" color="textSecondary">
                    Priority: {currentIssue.priority}
                </Typography>
                <Typography variant="body2" color="textSecondary">
                    Status: {currentIssue.status}
                </Typography>
                <Typography variant="body2" color="textSecondary">
                    Keywords: {formattedKeywords}
                </Typography>
            </Box>
            <Divider sx={{ my: 2 }} />
            <TableContainer sx={{ maxHeight: 440, borderTop: '1px solid rgba(224, 224, 224, 1)' }}>
                <Table stickyHeader aria-label="sticky table">
                    <TableHead>
                        <TableRow>
                            {columns.map((column) => (
                                <TableCell
                                    key={column.id}
                                    align={column.align}
                                    style={{ minWidth: column.minWidth, fontWeight: 'bold' }}
                                >
                                    {column.label}
                                </TableCell>
                            ))}
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {displayedRows.map((row) => (
                            <StyledTableRow tabIndex={-1} key={row.id}>
                                {columns.map((column) => {
                                    const value = row[column.id];
                                    return (
                                        <TableCell key={column.id} align={column.align}>
                                            {value}
                                        </TableCell>
                                    );
                                })}
                            </StyledTableRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>
            {displayedRows.length < rows.length && (
                <Box sx={{ display: 'flex', justifyContent: 'center', marginTop: 2 }}>
                    <Button onClick={handleLoadMore} color="success">
                        
                    </Button>
                </Box>
            )}
            <Box sx={{ display: 'flex', alignItems: 'center', marginTop: 2 }}>
                <TextField
                    label=" "
                    value={newComment}
                    onChange={(e) => setNewComment(e.target.value)}
                    fullWidth
                    margin="normal"
                    variant="outlined"
                    color="success"
                    sx={{
                        '& .MuiOutlinedInput-root': {
                            '&.Mui-focused fieldset': {
                                borderColor: '#03C75A', //    
                            },
                            '& .MuiInputLabel-root': {
                                color: '#03C75A', //   
                            },
                        },
                        marginRight: 2
                    }}
                />
                <Button onClick={handleCommentSubmit} variant="contained" sx={{
                    marginTop: 1,
                    backgroundColor: '#03C75A', //  
                    '&:hover': {
                        backgroundColor: '#03C75A', //   
                    },
                }}>
                    
                </Button>
            </Box>
        </Paper>
    );
};

export default IssueDetail;
